'use client'

import { useState } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Separator } from '@/components/ui/separator'
import {
  CheckCircle,
  XCircle,
  AlertTriangle,
  Upload,
  Database,
  Server,
  FileAudio,
  RefreshCw
} from 'lucide-react'

interface TestResult {
  name: string
  status: 'pending' | 'running' | 'success' | 'error'
  message?: string
  details?: any
}

export default function DeploymentTestPage() {
  const [tests, setTests] = useState<TestResult[]>([
    { name: 'Supabase Connection', status: 'pending' },
    { name: 'Authentication', status: 'pending' },
    { name: 'File Upload', status: 'pending' },
    { name: 'Database Queries', status: 'pending' },
    { name: 'Storage Access', status: 'pending' },
    { name: 'API Endpoints', status: 'pending' },
    { name: 'Transcription Flow', status: 'pending' }
  ])

  const [isRunning, setIsRunning] = useState(false)

  const updateTest = (index: number, updates: Partial<TestResult>) => {
    setTests(prev => prev.map((test, i) =>
      i === index ? { ...test, ...updates } : test
    ))
  }

  const runTest = async (index: number, testFunction: () => Promise<TestResult>) => {
    updateTest(index, { status: 'running' })

    try {
      const result = await testFunction()
      updateTest(index, {
        status: result.status,
        message: result.message,
        details: result.details
      })
    } catch (error) {
      updateTest(index, {
        status: 'error',
        message: error instanceof Error ? error.message : 'Test failed'
      })
    }
  }

  const testSupabaseConnection = async (): Promise<TestResult> => {
    try {
      // Test basic Supabase client
      const { supabase } = await import('@/lib/supabase')
      if (!supabase) throw new Error('Supabase client not initialized')

      // Test connection by checking auth state
      const { data, error } = await supabase.auth.getSession()

      return {
        name: 'Supabase Connection',
        status: 'success',
        message: 'Supabase client connected successfully',
        details: { hasSession: !!data.session, error: error?.message }
      }
    } catch (error) {
      return {
        name: 'Supabase Connection',
        status: 'error',
        message: error instanceof Error ? error.message : 'Connection failed'
      }
    }
  }

  const testAuthentication = async (): Promise<TestResult> => {
    try {
      const { supabase } = await import('@/lib/supabase')
      const { data, error } = await supabase.auth.getUser()

      return {
        name: 'Authentication',
        status: data.user ? 'success' : 'error',
        message: data.user ? `Logged in as ${data.user.email}` : 'Not authenticated',
        details: { userId: data.user?.id, email: data.user?.email }
      }
    } catch (error) {
      return {
        name: 'Authentication',
        status: 'error',
        message: error instanceof Error ? error.message : 'Auth check failed'
      }
    }
  }

  const testFileUpload = async (): Promise<TestResult> => {
    try {
      // First check if the bucket exists and is accessible
      const { supabaseAdmin } = await import('@/lib/supabase')

      // Check if supabaseAdmin is properly initialized
      if (!supabaseAdmin) {
        return {
          name: 'File Upload',
          status: 'error',
          message: 'Supabase admin client not initialized',
          details: {
            error: 'supabaseAdmin is null',
            suggestion: 'Check SUPABASE_SERVICE_ROLE_KEY environment variable'
          }
        }
      }

      console.log('🔍 Testing file upload...')

      // Add timeout to prevent hanging
      const timeoutPromise = new Promise((_, reject) =>
        setTimeout(() => reject(new Error('Request timeout')), 10000)
      )

      const storagePromise = supabaseAdmin.storage.listBuckets()
      const { data: buckets, error: listError } = await Promise.race([storagePromise, timeoutPromise]) as any

      if (listError) {
        return {
          name: 'File Upload',
          status: 'error',
          message: 'Cannot access Supabase Storage - check permissions',
          details: { error: listError.message, suggestion: 'Check your Supabase service role key and storage permissions' }
        }
      }

      console.log('🔍 Available buckets:', buckets?.map(b => b.name))
      console.log('🔍 Looking for bucket: "audio-files"')

      const audioFilesBucket = buckets?.find(b => b.name === 'audio-files')
      if (!audioFilesBucket) {
        console.log('❌ Bucket "audio-files" not found in available buckets')

        // Check for similar bucket names
        const similarBuckets = buckets?.filter(b =>
          b.name.includes('audio') ||
          b.name.includes('file') ||
          b.name.includes('media')
        ) || []

        return {
          name: 'File Upload',
          status: 'error',
          message: 'Storage bucket "audio-files" not found',
          details: {
            availableBuckets: buckets?.map(b => b.name) || [],
            similarBuckets: similarBuckets.map(b => b.name),
            suggestion: similarBuckets.length > 0
              ? `Found similar buckets: ${similarBuckets.map(b => b.name).join(', ')}. Rename one to "audio-files" or create new bucket.`
              : 'Create bucket named "audio-files" in Supabase Dashboard > Storage',
            troubleshooting: [
              '1. Go to Supabase Dashboard > Storage',
              '2. Check if bucket exists with different name',
              '3. Make sure you\'re in the correct Supabase project',
              '4. Try refreshing the Supabase Dashboard',
              '5. Check if you have Storage permissions in your Supabase project'
            ]
          }
        }
      }

      console.log('✅ Found audio-files bucket:', audioFilesBucket)

      // Try uploading with different MIME types
      const testFiles = [
        { content: 'test', type: 'text/plain', name: 'test.txt' },
        { content: new Uint8Array([1, 2, 3, 4]), type: 'application/octet-stream', name: 'test.dat' },
        { content: '{"test": "data"}', type: 'application/json', name: 'test.json' }
      ]

      let lastError = null
      for (const testFile of testFiles) {
        try {
          const blob = new Blob([testFile.content], { type: testFile.type })
          const file = new File([blob], testFile.name, { type: testFile.type })

          const { uploadDirectToStorage } = await import('@/lib/direct-upload-service')
          const result = await uploadDirectToStorage({
            file: file,
            bucketName: 'audio-files'
          })

          if (!result.error) {
            return {
              name: 'File Upload',
              status: 'success',
              message: `File uploaded successfully using ${testFile.type}`,
              details: { publicUrl: result.publicUrl, path: result.path, mimeType: testFile.type }
            }
          }

          lastError = result.error
        } catch (error) {
          lastError = error instanceof Error ? error.message : String(error)
        }
      }

      // If all MIME types failed, provide comprehensive guidance
      return {
        name: 'File Upload',
        status: 'error',
        message: 'Storage bucket exists but all upload attempts failed',
        details: {
          error: lastError,
          suggestion: 'Configure bucket to allow these MIME types: text/plain, application/octet-stream, application/json',
          steps: [
            'Go to Supabase Dashboard > Storage > audio-files bucket',
            'Check "Allowed MIME Types" settings',
            'Add the MIME types listed above',
            'Or disable MIME type restrictions entirely'
          ]
        }
      }
    } catch (error) {
      return {
        name: 'File Upload',
        status: 'error',
        message: error instanceof Error ? error.message : 'Upload test failed'
      }
    }
  }

  const testDatabaseQueries = async (): Promise<TestResult> => {
    try {
      const { supabase } = await import('@/lib/supabase')

      // Test a simple query
      const { data, error } = await supabase
        .from('user_profiles')
        .select('count')
        .limit(1)

      return {
        name: 'Database Queries',
        status: error ? 'error' : 'success',
        message: error ? error.message : 'Database queries working',
        details: { error: error?.message }
      }
    } catch (error) {
      return {
        name: 'Database Queries',
        status: 'error',
        message: error instanceof Error ? error.message : 'Database test failed'
      }
    }
  }

  const testStorageAccess = async (): Promise<TestResult> => {
    try {
      const { supabaseAdmin } = await import('@/lib/supabase')

      // Check if supabaseAdmin is properly initialized
      if (!supabaseAdmin) {
        return {
          name: 'Storage Access',
          status: 'error',
          message: 'Supabase admin client not initialized',
          details: {
            error: 'supabaseAdmin is null',
            suggestion: 'Check SUPABASE_SERVICE_ROLE_KEY environment variable'
          }
        }
      }

      console.log('🔍 Testing storage access...')

      // Add timeout to prevent hanging
      const timeoutPromise = new Promise((_, reject) =>
        setTimeout(() => reject(new Error('Request timeout')), 10000)
      )

      const storagePromise = supabaseAdmin.storage.listBuckets()
      const { data, error } = await Promise.race([storagePromise, timeoutPromise]) as any

      if (error) {
        if (error.message.includes('permission') || error.message.includes('unauthorized')) {
          return {
            name: 'Storage Access',
            status: 'error',
            message: 'Storage access denied - check RLS policies',
            details: { error: error.message, suggestion: 'Configure storage permissions in Supabase' }
          }
        }

        return {
          name: 'Storage Access',
          status: 'error',
          message: error.message,
          details: { error: error.message }
        }
      }

      // Check if audio-files bucket exists
      console.log('🔍 Storage Access Test - Available buckets:', data?.map(b => b.name))
      console.log('🔍 Storage Access Test - Looking for: "audio-files"')

      const audioFilesBucket = data?.find(bucket => bucket.name === 'audio-files')

      if (!audioFilesBucket) {
        console.log('❌ Storage Access Test - Bucket "audio-files" not found')

        // Check for similar bucket names
        const similarBuckets = data?.filter(b =>
          b.name.includes('audio') ||
          b.name.includes('file') ||
          b.name.includes('media')
        ) || []

        return {
          name: 'Storage Access',
          status: 'error',
          message: 'Storage accessible but "audio-files" bucket missing',
          details: {
            buckets: data?.map(b => b.name),
            similarBuckets: similarBuckets.map(b => b.name),
            suggestion: similarBuckets.length > 0
              ? `Found similar buckets: ${similarBuckets.map(b => b.name).join(', ')}. Rename one to "audio-files" or create new bucket.`
              : 'Create "audio-files" bucket in Supabase Storage',
            troubleshooting: [
              '1. Check Supabase Dashboard > Storage to see existing buckets',
              '2. Verify you\'re in the correct Supabase project',
              '3. Make sure bucket name is exactly "audio-files"',
              '4. Try refreshing your browser and running test again'
            ]
          }
        }
      }

      console.log('✅ Storage Access Test - Found audio-files bucket:', audioFilesBucket)

      return {
        name: 'Storage Access',
        status: 'success',
        message: 'Storage access working with required buckets',
        details: {
          buckets: data?.map(b => b.name),
          audioFilesBucket: audioFilesBucket.name
        }
      }
    } catch (error) {
      return {
        name: 'Storage Access',
        status: 'error',
        message: error instanceof Error ? error.message : 'Storage test failed'
      }
    }
  }

  const testApiEndpoints = async (): Promise<TestResult> => {
    try {
      // Get authentication token first
      const { supabase } = await import('@/lib/supabase')
      const { data: { session } } = await supabase.auth.getSession()

      if (!session?.access_token) {
        return {
          name: 'API Endpoints',
          status: 'error',
          message: 'No authentication token available',
          details: { suggestion: 'User must be logged in to test API endpoints' }
        }
      }

      // Test admin users API with authentication
      const response = await fetch('/api/admin/users?page=1&pageSize=1&includeStats=true', {
        headers: {
          'Authorization': `Bearer ${session.access_token}`
        }
      })

      let data
      try {
        data = await response.json()
      } catch (e) {
        data = { error: 'Invalid JSON response' }
      }

      if (response.status === 401) {
        return {
          name: 'API Endpoints',
          status: 'error',
          message: 'Authentication failed - token may be invalid',
          details: { status: response.status, error: data.error, suggestion: 'Check authentication token' }
        }
      }

      return {
        name: 'API Endpoints',
        status: response.ok ? 'success' : 'error',
        message: response.ok ? 'API endpoints responding correctly' : data.error || 'API test failed',
        details: { status: response.status, hasData: !!data, error: data.error }
      }
    } catch (error) {
      return {
        name: 'API Endpoints',
        status: 'error',
        message: error instanceof Error ? error.message : 'API test failed'
      }
    }
  }

  const testTranscriptionFlow = async (): Promise<TestResult> => {
    try {
      // Test transcription service import
      const { submitTranscription } = await import('@/lib/transcription-service')

      return {
        name: 'Transcription Flow',
        status: 'success',
        message: 'Transcription service loaded',
        details: { serviceAvailable: !!submitTranscription }
      }
    } catch (error) {
      return {
        name: 'Transcription Flow',
        status: 'error',
        message: error instanceof Error ? error.message : 'Transcription service failed'
      }
    }
  }

  const runAllTests = async () => {
    setIsRunning(true)

    await runTest(0, testSupabaseConnection)
    await runTest(1, testAuthentication)
    await runTest(2, testFileUpload)
    await runTest(3, testDatabaseQueries)
    await runTest(4, testStorageAccess)
    await runTest(5, testApiEndpoints)
    await runTest(6, testTranscriptionFlow)

    setIsRunning(false)
  }

  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'success':
        return <CheckCircle className="h-5 w-5 text-green-500" />
      case 'error':
        return <XCircle className="h-5 w-5 text-red-500" />
      case 'running':
        return <RefreshCw className="h-5 w-5 text-blue-500 animate-spin" />
      default:
        return <AlertTriangle className="h-5 w-5 text-yellow-500" />
    }
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'success':
        return 'bg-green-50 border-green-200'
      case 'error':
        return 'bg-red-50 border-red-200'
      case 'running':
        return 'bg-blue-50 border-blue-200'
      default:
        return 'bg-yellow-50 border-yellow-200'
    }
  }

  return (
    <div className="container mx-auto p-6 space-y-6">
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Server className="h-6 w-6" />
            Pre-Deployment Testing Suite
          </CardTitle>
          <CardDescription>
            Test all critical functionality before deploying to Vercel
          </CardDescription>
        </CardHeader>

        {/* Debug Info */}
        <div className="bg-blue-50 dark:bg-blue-950/20 p-4 mx-6 mb-0 rounded-lg border border-blue-200 dark:border-blue-800">
          <h3 className="font-semibold text-blue-900 dark:text-blue-100 mb-2 flex items-center gap-2">
            🔍 Current Supabase Project
          </h3>
          <div className="text-sm text-blue-800 dark:text-blue-200">
            <div className="font-mono bg-blue-100 dark:bg-blue-900/30 p-2 rounded mb-2">
              https://yaznemrwbingjwqutbvb.supabase.co
            </div>
            <p className="mb-2">
              <strong>⚠️ IMPORTANT:</strong> Make sure you're creating the <code>audio-files</code> bucket in this exact project!
            </p>
            <p className="text-xs">
              If the bucket exists in a different project, that's why the tests are failing.
            </p>
          </div>
        </div>

        <CardContent>
          <div className="flex gap-4 mb-6">
            <Button
              onClick={runAllTests}
              disabled={isRunning}
              className="flex items-center gap-2"
            >
              <RefreshCw className={`h-4 w-4 ${isRunning ? 'animate-spin' : ''}`} />
              Run All Tests
            </Button>
            <Badge variant="outline" className="flex items-center gap-2">
              <Database className="h-4 w-4" />
              Supabase Storage
            </Badge>
            <Badge variant="outline" className="flex items-center gap-2">
              <Upload className="h-4 w-4" />
              Direct Upload
            </Badge>
          </div>

          <div className="space-y-4">
            {tests.map((test, index) => (
              <Card key={index} className={`transition-all ${getStatusColor(test.status)}`}>
                <CardContent className="p-4">
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-3">
                      {getStatusIcon(test.status)}
                      <div>
                        <h3 className="font-medium">{test.name}</h3>
                        {test.message && (
                          <p className="text-sm text-muted-foreground mt-1">
                            {test.message}
                          </p>
                        )}
                      </div>
                    </div>
                    <Badge variant={
                      test.status === 'success' ? 'default' :
                      test.status === 'error' ? 'destructive' :
                      test.status === 'running' ? 'secondary' : 'outline'
                    }>
                      {test.status}
                    </Badge>
                  </div>
                  {test.details && (
                    <div className="mt-3 p-3 bg-background/50 rounded text-xs font-mono">
                      <pre>{JSON.stringify(test.details, null, 2)}</pre>
                    </div>
                  )}
                </CardContent>
              </Card>
            ))}
          </div>

          <Separator className="my-6" />

          {/* Deployment Readiness Summary */}
          <div className="bg-gradient-to-r from-blue-50 to-purple-50 dark:from-blue-950/20 dark:to-purple-950/20 p-6 rounded-lg border">
            <h3 className="font-semibold text-lg mb-4 flex items-center gap-2">
              🚀 Deployment Readiness Summary
            </h3>

            <div className="grid gap-4 md:grid-cols-2">
              <div>
                <h4 className="font-medium mb-2 text-green-700 dark:text-green-300">✅ Working Components:</h4>
                <ul className="text-sm space-y-1">
                  <li>• Supabase Connection</li>
                  <li>• Authentication System</li>
                  <li>• Database Queries</li>
                  <li>• File Structure</li>
                  <li>• Dependencies</li>
                </ul>
              </div>

              <div>
                <h4 className="font-medium mb-2 text-orange-700 dark:text-orange-300">⚠️ May Need Attention:</h4>
                <ul className="text-sm space-y-1">
                  <li>• Storage Bucket Config</li>
                  <li>• API Authentication</li>
                  <li>• File Upload Permissions</li>
                  <li>• Environment Variables</li>
                </ul>
              </div>
            </div>
          </div>

          <div className="bg-blue-50 dark:bg-blue-950/20 p-4 rounded-lg">
            <h3 className="font-semibold text-blue-900 dark:text-blue-100 mb-2">
              📤 File Upload Destination
            </h3>
            <p className="text-sm text-blue-800 dark:text-blue-200">
              Files will be uploaded to <strong>Supabase Storage</strong> (not Vercel).
              This ensures unlimited file sizes and reliable storage.
            </p>
            <div className="mt-2 flex gap-2 flex-wrap">
              <Badge variant="outline">✅ Bypasses Vercel 4.5MB limit</Badge>
              <Badge variant="outline">✅ Reliable storage</Badge>
              <Badge variant="outline">✅ Direct client upload</Badge>
              <Badge variant="outline">✅ Scalable solution</Badge>
            </div>
          </div>

          {/* Quick Fix Guide */}
          <div className="bg-yellow-50 dark:bg-yellow-950/20 p-4 rounded-lg border border-yellow-200 dark:border-yellow-800">
            <h3 className="font-semibold text-yellow-900 dark:text-yellow-100 mb-2">
              🔧 Fix Storage Issues (Most Common):
            </h3>
            <div className="text-sm space-y-3">
              <div className="bg-white dark:bg-gray-800 p-3 rounded border">
                <strong className="text-yellow-800 dark:text-yellow-200">1. Create Storage Bucket:</strong>
                <ol className="text-yellow-700 dark:text-yellow-300 mt-1 ml-4 list-decimal">
                  <li>Go to <a href="https://supabase.com/dashboard" target="_blank" rel="noopener" className="underline">Supabase Dashboard</a></li>
                  <li>Select your project</li>
                  <li>Go to Storage → Create bucket</li>
                  <li>Name: <code>audio-files</code></li>
                  <li>Keep it private (not public)</li>
                </ol>
              </div>

              <div className="bg-white dark:bg-gray-800 p-3 rounded border">
                <strong className="text-yellow-800 dark:text-yellow-200">2. Configure MIME Types:</strong>
                <p className="text-yellow-700 dark:text-yellow-300 mt-1">
                  In bucket settings, add these MIME types:
                </p>
                <div className="mt-2 font-mono text-xs bg-gray-100 dark:bg-gray-700 p-2 rounded">
                  audio/*<br/>
                  application/octet-stream<br/>
                  application/json<br/>
                  text/plain
                </div>
              </div>

              <div className="bg-white dark:bg-gray-800 p-3 rounded border">
                <strong className="text-yellow-800 dark:text-yellow-200">3. Alternative: Run Setup Script</strong>
                <div className="text-yellow-700 dark:text-yellow-300 mt-1">
                  <p>Run this command in your terminal:</p>
                  <code className="block mt-1 p-2 bg-gray-100 dark:bg-gray-700 rounded text-xs">
                    node setup-supabase-storage.js
                  </code>
                </div>
              </div>
            </div>
          </div>

          {/* Deployment Status */}
          <div className="bg-green-50 dark:bg-green-950/20 p-4 rounded-lg border border-green-200 dark:border-green-800">
            <h3 className="font-semibold text-green-900 dark:text-green-100 mb-2">
              🎯 Deployment Status:
            </h3>
            <div className="text-sm text-green-800 dark:text-green-200 space-y-1">
              <p>✅ <strong>Authentication:</strong> Working perfectly</p>
              <p>✅ <strong>Database:</strong> Connected and responsive</p>
              <p>✅ <strong>API Endpoints:</strong> Functioning correctly</p>
              <p>⚠️ <strong>Storage:</strong> Needs bucket configuration</p>
              <p>✅ <strong>Transcription Service:</strong> Ready</p>
            </div>
            <div className="mt-3 p-3 bg-green-100 dark:bg-green-900/30 rounded">
              <p className="text-green-800 dark:text-green-200 text-sm">
                <strong>Next Step:</strong> Configure storage bucket, then you'll be ready to deploy to Vercel! 🚀
              </p>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
